$cWhite:rgba(255,255,255,1);
$cBlack:rgba(0,0,0,1);
$cGray:rgba(239,239,239,1);
$c4a4:#4A4A4A;
$cBrawn:#614C21;
$cOrange:#CBA054;
$c9b9:#9B9B9B;
a, abbr, address, article, aside, audio, b, blockquote, body, caption, cite, code, dd, del, dfn, dialog, div, dl, dt, em, fieldset, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, hr, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, p, pre, q, samp, section, small, span, strong, sub, tfoot, th, thead, time, tr, ul, var, video {
    border: 0;
    margin: 0;
    outline: 0;
    padding: 0;
    font-size: 100%;
    font-weight: normal;
}
body {
    margin: 0;
    padding: 0;
    text-align: center;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-text-size-adjust:none;
    color:$c4a4;
    background:$cWhite;
    font-family:PingFangSC,'helvetica neue','hiragino sans gb',arial,'microsoft yahei ui','microsoft yahei',Heiti,simsun,sans-serif;
    font-size:14px;
    background: $cGray;
}
em{
    font-style: normal;
}
html{
    -webkit-text-size-adjust: none;
}
html,body{
    height: 100%;
    width: 100%;
}
body * {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    -webkit-tap-highlight-color: rgba(50, 50, 50, 0);
    -webkit-font-smoothing: antialiased;
}

a {
    text-decoration: none;
    color: #333;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
img {
    border: 0;
    -ms-interpolation-mode: bicubic;
    vertical-align: middle;
    font-size: 0
}
li{
    list-style: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0
}
th, td, caption {
    vertical-align: top;
    text-align: left
}

sub,sup{
    font-size: 10px;
}

input[type="text"],input[type="number"],input[type="tel"]{
    padding:12px 5px;
    border:none;
    height: 38px;
    line-height: 14px;
    font-size: 14px;
}
textarea{
    border:1px solid #ccc;
    padding:0;
    display: block;
    width: 100%;
    height: 100px;
    border:none;
    line-height: 20px;
    font-size: 14px;
}
button{
    text-align: center;
    border: none;
    border-radius: 3px;
    line-height: 40px;
    font-size: 14px;
}
%cssbox{
    display:-moz-box;
    display:-webkit-box;
    display:box;
}
%cssboxflex{
        -moz-box-flex:1.0;
        -webkit-box-flex:1.0;
        box-flex:1.0;
}
%aligncenter{
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    -webkit-box-pack: center;
    -moz-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
}
header{
    line-height: 44px;
    color: $cWhite;
    font-size: 18px;
    .fa{
        position: absolute;
        left:10px;
        line-height: 44px;
        top: 0;
        z-index: 1;
    }
}
%fl{
    float: left;
}
%fr{
    float: right;
}
.fl{
    float: left;
}
.fr{
    float: right;
}
.home{
    background: url(img/home-bg.png) left top no-repeat;
    background-size: cover;
    height: 160px;
    position: relative;
    text-align: left;
    .home-space{
        @extend %cssbox;
        padding: 0 15px;
        img{
            width: 76px;
            height: 76px;
            border-radius: 50%;
            border:2px solid $cWhite;
            margin:30px 0 0;
        }
        div{
            @extend %cssboxflex;
            margin:40px 0 0 10px;
            font-size: 24px;
            span{
                display: block;
                height: 20px;
                margin:5px 0 0;
            }
            .rz-no{
                background: url(img/rz-no.png) left top no-repeat;
                background-size:cover;
                width: 63px;
            }
            .rz-ok{
                background: url(img/rz-ok.png) left top no-repeat;
                background-size:cover;
                width: 80px;
            }
        }
        button{
            display: block;
            margin:50px 0 0;
            width: 80px;
            height: 34px;
            line-height: 32px;
            border:1px solid $cBrawn;
            border-radius: 4;
            color: $cBrawn;
            background: none;
        }
    }
    .home-stat{
        height: 40px;
        line-height: 40px;
        position: absolute;
        left:0;
        right: 0;
        bottom: 0;
        padding: 0 15px;
        color: $cBrawn;
        background: rgba(0,0,0,.1);
        a{
            color: $cBrawn;
            line-height: 40px;
            @extend %fr;
            &:after{
                content: "\f054";
            }
        }
    }
}
.home-link{
    background: $cWhite;
    margin:0 0 10px;
    text-align: left;
    ul{
        padding: 0 20px;
    }
    li{
        line-height: 50px;
        border-bottom: 1px solid #F5F5F5;
        font-size: 16px;
        padding: 0 0 0 30px;
        &:last-child(){
            border: none;
        }
        .fa{
            line-height: 50px;
            color: #D7D7D7;
        }
        button{
            float: right;
            border:1px solid #DBA54A;
            height: 30px;
            width: 50px;
            background: none;
            color: #DBA54A;
            line-height: 30px;
            margin:8px 5px 0;
        }
    }
    .icon-h1{background: url(img/icon-home1.png) left center no-repeat;background-size: auto 40%;}
    .icon-h2{background: url(img/icon-home2.png) left center no-repeat;background-size: auto 40%;}
    .icon-h3{background: url(img/icon-home3.png) left center no-repeat;background-size: auto 40%;}
    .icon-h4{background: url(img/icon-home4.png) left center no-repeat;background-size: auto 40%;}
    .icon-h5{background: url(img/icon-home5.png) left center no-repeat;background-size: auto 40%;}
    .icon-h6{background: url(img/icon-home6.png) left center no-repeat;background-size: auto 40%;}
    .icon-h7{background: url(img/icon-home7.png) left center no-repeat;background-size: auto 40%;}
    .icon-h8{background: url(img/icon-home8.png) left center no-repeat;background-size: auto 40%;}
    .icon-h9{background: url(img/icon-home9.png) left center no-repeat;background-size: auto 40%;}
    .icon-h10{background: url(img/icon-home10.png) left center no-repeat;background-size: auto 40%;}
    .icon-h11{background: url(img/icon-home11.png) left center no-repeat;background-size: auto 40%;}
}
.home-price-all{
    @extend %cssbox;
    background: $cWhite;
    margin:0 0 10px;
    .all-b{
        @extend %cssboxflex;
        width: 1%;
        padding: 20px 0;
        strong{
            display: block;
            font-size: 32px;
            color: $cOrange;
            small{
                font-size: 12px;
            }
        }
    }
}
.home-chart{
    background: $cWhite;
    content: "图表";
    padding: 10px;
    margin:0 0 10px;
}
.home-price-detail{
    background: $cWhite;
    padding: 20px 10px;
    margin:0 0 10px;
    strong{
        display: block;
        font-size: 32px;
        color: $cOrange;
        margin:10px 0 0;
        small{
            font-size: 12px;
        }
    }
    span{
        font-size: 12px;
    }
    button{
        border:1px solid $cOrange;
        background: none;
        color: $cOrange;
        padding: 0 10px;
        margin:10px 5px;
        height: 30px;
        line-height: 30px;
    }
    .actived{
        background: $cOrange;
        color: $cWhite;
    }
}
.home-link2{
    @extend %cssbox;
    background: $cWhite;
    border-bottom:1px solid $cGray;
    line-height: 60px;
    text-align: left;
    a{
        display: block;
        @extend %cssboxflex;
        border-right:1px solid $cGray;
        padding: 0 0 0 50px;
    }
    .icon-l-1{background: url(img/icon-l-1.png) 20px center no-repeat;background-size: auto 40%;}
    .icon-l-2{background: url(img/icon-l-2.png) 20px center no-repeat;background-size: auto 40%;}
    .icon-l-3{background: url(img/icon-l-3.png) 20px center no-repeat;background-size: auto 40%;}
    .icon-l-4{background: url(img/icon-l-4.png) 20px center no-repeat;background-size: auto 40%;}
    .icon-l-5{background: url(img/icon-l-5.png) 20px center no-repeat;background-size: auto 40%;}
    .icon-l-6{background: url(img/icon-l-6.png) 20px center no-repeat;background-size: auto 40%;}
    .icon-l-7{background: url(img/icon-l-7.png) 20px center no-repeat;background-size: auto 40%;}
    .icon-l-8{background: url(img/icon-l-8.png) 20px center no-repeat;background-size: auto 40%;}
}
.bar-box{
    clear: both;
    height: 50px;
}
.bar{
    position: fixed;
    background: $cWhite;
    bottom: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 50px;
    box-shadow: 0 -5px 5px #eee;
    @extend %cssbox;
    a{
        display: block;
        @extend %cssboxflex;
        width: 1%;
        padding: 27px 0 0;
    }
    .bar-1{background: url(img/icon-bar-1.png) center 7px no-repeat;background-size: auto 40%;}
    .bar-2{background: url(img/icon-bar-2.png) center 7px no-repeat;background-size: auto 40%;}
    .bar-3{background: url(img/icon-bar-3.png) center 7px no-repeat;background-size: auto 40%;}
    .bar-4{background: url(img/icon-bar-4.png) center 7px no-repeat;background-size: auto 40%;}
    .bar-1.actived{background: url(img/icon-bar-1-on.png) center 7px no-repeat;background-size: auto 40%;color: #E03635;}
    .bar-2.actived{background: url(img/icon-bar-2-on.png) center 7px no-repeat;background-size: auto 40%;color: #E03635;}
    .bar-3.actived{background: url(img/icon-bar-3-on.png) center 7px no-repeat;background-size: auto 40%;color: #E03635;}
    .bar-4.actived{background: url(img/icon-bar-4-on.png) center 7px no-repeat;background-size: auto 40%;color: #E03635;}
}
.form-box{
    background: $cWhite;
    margin:0 0 10px;
    line-height: 50px;
    ul{
        padding: 10px 15px;
    }
    li{
        @extend %cssbox;
        label{
            display: block;
            width: 70px;
            padding: 0 10px 0 0;
            text-align: right;
        }
        input{
            @extend %cssboxflex;
            margin:8px 0 0;
            display: block;
            background: #F7F7F7;
            border:1px solid #F1F1F1;
            height: 33px;
            line-height: 33px;
            vertical-align: middle;
        }
        button{
            margin:8px 0 0;
            display: block;
            background: $cOrange;
            height: 33px;
            line-height: 33px;
            color: $cWhite;
            border-radius: 0;
            padding: 0 10px;
        }
        div{
            @extend %cssboxflex;
            text-align: left;
        }
        a{
            width: 70px;
            display: block;
            color: $cOrange;
            line-height: 50px;
        }
    }
}
.form-check{
    text-align: left;
    padding: 0 15px;
    input,label{
        vertical-align: middle;
        margin:0 5px 0 0;
    }
}
.bar-btn{
    position: fixed;
    background: $cWhite;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    button{
        background: #CBA054;
        line-height: 50px;
        color: $cWhite;
        display: block;
        width: 100%;
        border-radius: 0;
    }
}
.form-btn{
    padding: 0 20px;
    background: $cWhite;
    button{
        background: #CBA054;
        line-height: 44px;
        color: $cWhite;
        display: block;
        width: 100%;
        border-radius: 2;
    }
}
.user-clause{
    text-align: left;
    padding: 0 20px;
}
.sumbit-success{
