    }
    .btn-gray{
        background: #F7F7F7;
        color: #666;
    }
}
.sumbit-pro{
    background: url(img/icon-pro.png) center 50px no-repeat $cWhite;
    background-size:88px;
}
.form-view{
    background: $cWhite;
    padding: 0 0 20px;
    ul{
        padding: 10px 15px;
        text-align: left;
    }
    li{
        @extend %cssbox;
        line-height: 36px;
        label{
            display: block;
            width: 70px;
            text-align: right;
            padding: 0 10px 0 0;
            color: #9B9B9B;
        }
        div{
            @extend %cssboxflex;
            img{
                width: 80%;
            }
        }
        a{
            display: block;
            color: $cOrange;
            line-height: 36px;
        }
    }
}
.take-money{
    text-align: center;
    background: $cWhite;
    padding: 20px;
    strong{
        font-weight: bold;
        font-size: 32px;
        color: $cOrange;
        display: block;
        padding: 20px 0;
        small{
            font-size: 14px;
        }
    }
    input{
        width: 100%;
        height: 33px;
        padding: 0 5px;
        line-height: 33px;
        background: $cGray;
        margin:20px 0;
    }
    button{
        background: $cOrange;
        color: $cWhite;
        width: 100%;
        line-height: 44px;
        border-radius: 4px;
    }
    .take-t{
        color: $c4a4;
        font-size: 20px;
    }
}
.tab{
    height: 44px;
    background: $cOrange;
    @extend %cssbox;
    a{
        display: block;
        @extend %cssboxflex;
        line-height: 30px;
        height: 30px;
        margin:6px 10px 0;
        color: $cWhite;
    }
    .actived{
        background: $cWhite;
        border-radius: 20px;
        color: $cOrange;
    }
}
.record-list{
    background: $cWhite;
    height: 100vh;
    padding: 10px 20px;
    .record{
        line-height: 24px;
        color:#666;
        text-align: left;
        border-bottom: 1px solid #F9F8F7;
        padding: 10px 0;
        @extend %cssbox;
    }
    .record-c{
        @extend %cssboxflex;
    }
    .record-m{
        width: 86px;
        color: #E03635;
        @extend %aligncenter;
        font-size: 16px;
    }
}
.my-pro{
    padding: 20px;
    background: $cWhite;
    text-align: left;
    margin:0 0 15px;
    .pro-t{
        font-size: 18px;
        color: $c4a4;
        small{
            color: $c9b9;
            font-size: 14px;
        }
        span{
