        }
        label{
            float: right;
            width: 60px;
            height: 25px;
            line-height: 25px;
            color: $cWhite;
            text-align: center;
        }
        .red{
            background: url(img/label-red.png) left top no-repeat;
            background-size: 100%;
        }
        .orange{
            background: url(img/label-orange.png) left top no-repeat;
            background-size: 100%;
        }
        &:after{
            content:".";
            display:block;
            height:0;
            visibility:hidden;
            clear:both;
