    }
}
.home-card{
    position: relative;
    color: #614C21;
    padding: 20px;
    strong{
        font-size: 24px;
    }
    p{
        margin:20px 0 0;
    }
    .rz-no{
        background: url(img/rz-no.png) left top no-repeat;
        background-size:cover;
        width: 63px;
        height: 20px;
        position: absolute;
        right: 20px;
        top: 30px;
        z-index: 1;
    }
