        }
        strong{
            display: block;
            color: $c4a4;
            font-size: 18px;
            padding: 5px 0;
            small{
                color: $c9b9;
                font-size: 14px;
            }
        }
        .red{
            color: #E03635;
        }
    }
    &:after{
        content:".";
        display:block;
        height:0;
        visibility:hidden;
        clear:both;
    }
}
